import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes: [
    {path: '/', redirect: '/home'},
    {
      path: '/home',
      name: 'home',
      meta: {
        hide: true,
        isAlone: true,
        title: '首页',
        notCache: true
      },
      component: () => import('@/views/home/home.vue')
    },
    {
      path: '/tesseract',
      name: 'tesseract',
      meta: {
        hide: true,
        isAlone: true,
        title: '提取图片文字',
        notCache: true
      },
      component: () => import('@/views/tesseract/tesseract.vue')
    }
  ]
});

export default router;
